body {
  background-color: #f0f0f0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  display: flex;
}

.cat-info {
  max-width: 1200px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.cat-card {
  width: 300px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 10px;
  transition: transform .2s;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.cat-card:hover {
  transform: scale(1.05);
}

.cat-image {
  width: 100%;
  height: auto;
}

.cat-details {
  padding: 15px;
}

.cat-name {
  margin: 10px 0;
  font-size: 1.5em;
}

.cat-description {
  color: #666;
  font-size: 1em;
}

.breed-select {
  max-width: 400px;
  margin: auto;
}

